.dark {
  background-color: #000;
  color: #f2f2f2;
}

.safearea {
  max-width: 87.2%;
  margin-right: auto;
  margin-left: auto;
}

main {
  overflow: hidden;
}

.hero {
  height: 100vh;
  display: flex;
  align-items: center;
  text-align: center;
}
.hero-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 120px auto 0;
  max-width: 370px;
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .hero-wrapper.safearea {
    margin: 0 auto 0;
    max-width: 670px;
  }
}
@media only screen and (min-width: 1200px) {
  .hero-wrapper.safearea {
    margin: 70px auto 0;
    max-width: 800px;
  }
}
.hero-title {
  font-size: 90px;
  font-weight: 800;
  line-height: 0.875;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
}
@media only screen and (min-width: 768px) {
  .hero-title {
    font-size: 64px;
    font-weight: 800;
    line-height: 0.875;
  }
}
@media only screen and (min-width: 1200px) {
  .hero-title {
    font-size: 140px;
    font-weight: 800;
    line-height: 0.875;
    min-width: 1090px;
  }
}
.hero-title > span {
  display: block;
  letter-spacing: 0;
}
@media only screen and (min-width: 768px) {
  .hero-title > span {
    display: inline-block;
    letter-spacing: -0.04em;
  }
}
@media only screen and (max-width: 767px) {
  .hero-title > span + span {
    letter-spacing: -0.08em;
  }
}
@media only screen and (max-width: 767px) {
  .hero-title > span + span + span {
    letter-spacing: -0.04em;
  }
}
.hero-list, .hero-list_item {
  width: 100%;
  margin: 0 auto;
}
.hero-list {
  position: absolute;
  list-style: none;
  padding: 0;
  height: 100%;
  top: 60px;
}
@media only screen and (min-width: 768px) {
  .hero-list {
    top: 30px;
  }
}
.hero-list_item {
  font-size: 40px;
  font-weight: 700;
  letter-spacing: -0.021em;
  line-height: 43px;
  position: absolute;
  opacity: 0;
  transform: translateY(30px);
}
@media only screen and (min-width: 768px) {
  .hero-list_item {
    font-size: 50px;
    font-weight: 700;
    letter-spacing: -0.021em;
    line-height: 53px;
  }
}
@media only screen and (min-width: 1200px) {
  .hero-list_item {
    font-size: 64px;
    font-weight: 700;
    letter-spacing: -0.021em;
    line-height: 67px;
  }
}
.hero-subtitle {
  font-size: 19px;
  font-weight: 500;
  letter-spacing: -0.013em;
  line-height: 25px;
  color: #333;
  margin-bottom: 0;
  opacity: 0;
  transform: translateY(30px);
}
@media only screen and (min-width: 768px) {
  .hero-subtitle {
    font-size: 21px;
    font-weight: 500;
    letter-spacing: -0.032em;
    line-height: 29px;
    max-width: 532px;
  }
}
@media only screen and (min-width: 1200px) {
  .hero-subtitle {
    font-size: 24px;
    font-weight: 500;
    letter-spacing: -0.032em;
    line-height: 31px;
    max-width: 632px;
    margin-top: 0;
  }
}
.will-change .hero-title, .will-change .hero-subtitle, .will-change .hero-list_item {
  will-change: transform, opacity;
}
.hero-cta {
  display: block;
  font-weight: 500;
  margin-top: 1rem;
  text-decoration: none;
  color: #0d6efd;
}
@media only screen and (min-width: 768px) {
  .hero-cta {
    display: inline-block;
    margin-top: 1rem;
    margin-right: 8px;
  }
}
.hero-cta + a {
  margin-top: 8px;
}